<template>
  <div>   
              <div v-if="zdTitle == ''" colcenter style="margin:5px 0;">
                <div v-if="!lshjShown" flex>
                    <p class="query-title">:</p>
                    <p class="query-list"  @click="handleYYRYLX">
                        <span data-type=""  :class="{'bg-active':lx == ''}"></span>
                        <span v-for="(yylx,index) in yyryLx" :data-type="yylx.label" :key="index" :class="{'bg-active':lx == yylx.label}">{{yylx.mc}}</span>
                    </p>
                 </div>
                 <div v-if="lshjShown" flex>
                    <p class="query-title" style="width:110px;">:</p>
                    <el-select v-model="lchj" placeholder="" size="mini" style="width:223px;">
                      <el-option v-for="lcbj in lchjList" :key="lcbj.value" :label="lcbj.mc" :value="lcbj.bh"></el-option>
                    </el-select>
                 </div>&#x3000;
                  <el-button type="info" size="mini" plain @click="chooseLchj">{{!lshjShown?'':''}}</el-button>
              </div>
              <div  flex class="mg-12" v-if="zdTitle == '' || zdTitle == ''">
                    <p>
                       <span class="query-title before-require" style="width:110px;">:</span>
                       <el-date-picker size="mini" v-model="qwjjrqZf"  type="date" placeholder=""  value-format="yyyy-MM-dd"></el-date-picker> 
                    </p>
              </div>
              <el-input v-if="zdTitle !== '' || !lshjShown" size="mini" v-model="cpKeyword" :placeholder="zdTitle === ''?'//':'/'" style="width:80%;margin:0 0 5px 0" @change="queryComplainUser"></el-input> &#x3000;
              <el-button v-if="zdTitle !== '' || !lshjShown" size="mini" type="primary" @click="queryComplainZRR"></el-button>
              <div v-if="zdTitle === ''">
                  <el-table
                    :data="tableData"
                    border
                    height="400px"
                    style="width: 100%">
                    <el-table-column label="" width="50">
                      <template slot-scope="scope">
                        <div slot="reference" class="radio-wrapper">
                          <el-radio  v-model="radio" :label="scope.$index"  @change.native="getCurrentRow(scope.$index,scope.row)">&nbsp;</el-radio>
                        </div>
                      </template>
                    </el-table-column>
                    <el-table-column  prop="cymc" label="" width="180"></el-table-column>
                    <el-table-column  prop="cybh" label="" width="180"></el-table-column>
                    <el-table-column  prop="cpxmc" label=""></el-table-column>
                  </el-table>
                  <div text-right>
                   <pagination v-if="jkrTotal>8 && jkrTotal != null" :total="jkrTotal" :pageSize="10" @handleCurrentChange="handleCurrentChange"></pagination>
                  </div>   
              </div>
              
              <div v-if="zdTitle === ''">
                <el-table
                    :data="tableData"
                    border
                    height="400px"
                    style="width: 100%">
                    <el-table-column label="" width="50">
                      <template slot-scope="scope">
                        <div slot="reference" class="radio-wrapper">
                          <el-radio  v-model="radio" :label="scope.$index"  @change.native="getCurrentRow(scope.$index,scope.row)">&nbsp;</el-radio>
                        </div>
                      </template>
                    </el-table-column>
                    <el-table-column  prop="nickName" label="" ></el-table-column>
                    <el-table-column  prop="userName" label="" ></el-table-column>
                  </el-table>
                  <div text-right>
                <pagination v-if="jkrTotal>8 && jkrTotal != null" :total="jkrTotal" :pageSize="10" @handleCurrentChange="handleCurrentChange"></pagination>
                </div> 
              </div>
              
              <div v-if="zdTitle === '' && !lshjShown" class="choose-yyryzf">
                  <el-table
                    :data="tableData"
                    border
                    height="400px"
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="55">
                    </el-table-column>
                    <el-table-column  prop="cymc" label="" width="180"></el-table-column>
                    <el-table-column  prop="cybh" label="" width="180"></el-table-column>
                    <el-table-column  prop="cpmc" label=""></el-table-column>
                  </el-table>
                  <div text-right>
                <pagination v-if="jkrTotal>8 && jkrTotal != null" :total="jkrTotal" :pageSize="10" @handleCurrentChange="handleCurrentChange"></pagination>      
                </div> 
              </div>

              <div v-if="zdTitle === ''" class="choose_cbr">
                  <el-table
                    :data="tableData"
                    border
                    height="400px"
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="55">
                    </el-table-column>
                    <el-table-column  prop="nickname" label="" ></el-table-column>
                    <el-table-column  prop="userbh" label=""></el-table-column>
                  </el-table>
              </div>
              <div v-if="zdTitle != ''" class="zdfzr-note">
                    <div id="summernoteZrr"> </div>
              </div>
              <div style="text-align:right" class="msg-12">
                <el-button size="mini" type="primary" @click="handleCommitZdr"></el-button>
                <el-button size="mini" type="info" @click="handleClose"></el-button>
              </div>
        </div>
</template>
<script>
import pagination from "@/components/BusinessPage/pagination.vue";
export default {
  data() {
    return {
      cpKeyword: "",
      radio: "",
      ryData: {},
      multipleSelection: [],
      lx: "",
      placeholder: "/",
      lchj: "",
      yyryLx: [
        {
          label: "0",
          mc: ""
        },
        {
          label: "1",
          mc: ""
        },
        {
          label: "2",
          mc: ""
        }
      ],
      lshjShown: false,
      qwjjrqZf: ""
    };
  },
  props: {
    tableData: {
      type: Array,
      default: function() {
        return [];
      }
    },
    yyryzfData: {
      type: Array,
      default: function() {
        return [];
      }
    },
    zdTitle: {
      type: String,
      default: ""
    },
    jkrTotal: {
      type: Number,
      default: 1
    },
    visible: {
      type: Boolean,
      default: true
    },
    lchjList: {
      type: Array,
      default: function() {
        return [];
      }
    }
  },
  methods: {
    chooseLchj() {
      this.lshjShown = !this.lshjShown;
      if (this.lshjShown) {
        this.multipleSelection = [];
      } else {
        this.lchj = "";
      }
    },
    handleYYRYLX(e) {
      let lx = e.target.getAttribute("data-type");
      if (lx == null) return;
      this.lx = lx;
      this.multipleSelection = [];
      this.$emit("handleYYRYLX", lx);
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    queryComplainZRR() {
      this.$emit("queryComplainZRR", this.cpKeyword);
      this.radio = "";
      if (this.zdTitle == "") {
        this.ryData.cymc = "";
      } else if (this.zdTitle == "") {
        this.ryData.nickName = "";
      } else if (this.zdTitle == "") {
        this.ryData.cymc = "";
        this.multipleSelection = [];
      } else if (this.zdTitle == "") {
        this.ryData.username = "";
        this.multipleSelection = [];
      }
    },

    queryComplainUser() {
      this.$emit("queryComplainUser", this.cpKeyword);
      this.radio = "";
      if (this.zdTitle == "") {
        this.ryData.cymc = "";
      } else if (this.zdTitle == "") {
        this.ryData.nickName = "";
      } else if (this.zdTitle == "") {
        this.ryData.cymc = "";
        this.multipleSelection = [];
      } else if (this.zdTitle == "") {
        this.ryData.username = "";
        this.multipleSelection = [];
      }
    },
    //
    handleCurrentChange(data) {
      this.$emit("handleCurrentChange", data);
    },
    // 
    getCurrentRow(index, row) {
      this.ryData = row;
    },
    handleCommitZdr() {
      this.ryData.nr =
        $("#summernoteZrr").summernote("code") == "<p><br></p>"
          ? ""
          : $("#summernoteZrr").summernote("code");
      let yyry = [];
      this.multipleSelection.forEach((ele, i, arr) => {
        if (this.zdTitle == "") {
          yyry.push(ele.username);
        } else {
          yyry.push(ele.cymc + "," + ele.cybh);
        }
      });
      this.ryData.yyryData = yyry;
      this.ryData.lchj = this.lchj; // 
      if (this.zdTitle == "" && !this.ryData.cymc) {
        this.$alert("" + this.zdTitle, "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      } else if (this.zdTitle == "") {
        if (!this.ryData.nickName) {
          this.$alert("" + this.zdTitle, "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        }
        if (!this.qwjjrqZf) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        }
        this.ryData.qwjjrq = this.qwjjrqZf;
      } else if (this.zdTitle == "") {
        if (!this.lshjShown && this.multipleSelection.length == 0) {
          this.$alert("" + this.zdTitle, "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        } else if (this.lshjShown && this.lchj == "") {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        } else if (!this.qwjjrqZf) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        }
        this.ryData.qwjjrq = this.qwjjrqZf;
      } else if (
        this.zdTitle == "" &&
        this.multipleSelection.length == 0
      ) {
        this.$alert(this.zdTitle, "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }
      this.$emit("handleCommitZdr", this.ryData);
    },
    handleClose() {
      this.$emit("handleClose", "");
    },
    getSummernote() {
      this.$nextTick(() => {
        $("#summernoteZrr").summernote({
          dialogsInBody: true,
          placeholder: "",
          height: 200,
          width: 780,
          minHeight: 200,
          maxHeight: 200,
          lang: "zh-CN",
          focus: true,
          toolbar: [
            ["style", ["bold", "italic", "underline", "clear"]],
            ["font", ["strikethrough", "superscript", "subscript"]],
            ["fontsize", ["fontsize"]],
            ["color", ["color"]],
            ["para", ["ul", "ol", "paragraph"]],
            ["height", ["height"]],
            ["picture"],
            ["link", ["linkDialogShow", "unlink"]]
          ]
        });
      });
    }
  },
  mounted() {
    this.getSummernote();
  },
  watch: {
    visible(n, o) {
      if (!n) {
        this.radio = "";
        this.ryData = {};
        this.multipleSelection = [];
        this.cpKeyword = "";
        $("#summernoteZrr").summernote("code", "");
      }
    },
    zdTitle(n, o) {
      if (n == "") {
        $("#summernoteZrr").summernote("destroy");
      } else {
        this.getSummernote();
      }
    }
  },
  components: { pagination }
};
</script>
<style scoped>
.name-wrapper span {
  color: #409eff;
}
.name-wrapper span:hover {
  cursor: pointer;
}
.radio-wrapper {
  padding-left: 5px;
}
.query-title {
  font-size: 14px;
  width: 50px;
  white-space: nowrap;
  text-align: left;
  padding: 3px 5px 0;
  font-weight: 700;
}
.query-list span:hover {
  cursor: pointer;
}
.query-list span {
  display: inline-block;
  padding: 0px 5px;
  color: #637d8a;
  border-radius: 3px;
  margin: 5px 10px 0 0;
}
.bg-active {
  background: #1997d7;
  color: #fff !important;
}
</style>



