// 
export function getDwByUser(obj){
	let url =  window.baseurl + suburl + 'getDwByUser.do'
	return axios.get(url,{
		params:{
			dwlx:obj.dwlx,
			curPage:obj.curPage,
			pageSize:obj.pageSize,
			keyword:obj.keyword
		}
	})
}

