export function saveMessageSwitch(obj){
	let url = window.baseurl +"user/saveMessageSwitch.do"
	return axios.post(url,{
			id:obj.id,
			value:obj.value,
	},{
		transformRequest: [function (data) {
data = Qs.stringify(data);
    return data;
  }]
})
}

