import axios from 'axios'
import Qs from 'qs'

var suburl = 'process/'
var fileurl = 'attachment/'

// 	
export function addOrUpdateTaskProcess(obj){
	let url = window.baseurl +suburl+"addOrUpdateTaskProcess.do"
	return axios.post(url,{
			xmbh:obj.xmbh,
			fbbh:obj.fbbh,
			rwbh:obj.rwbh,
			gclx:obj.gclx,
			gcms:obj.gcms,
			gcrq:obj.gcrq,
			gs:obj.gs,
			hfrbh:obj.hfrbh,
			hfrxm:obj.hfrxm,
			fjdata:obj.fjdata||"",
			wid:obj.wid||"",
			jdmc:obj.jdmc, //
			gccpmc:obj.gccpmc,
			gcrwmc:obj.gcrwmc
	},{
		transformRequest: [function (data) {
data = Qs.stringify(data);
    return data;
  }]
})
}


// 
export function queryLogTaskProcess(obj){
	let url =  window.baseurl + suburl + 'queryLogTaskProcess.do'
	return axios.get(url,{
		params:{
			curPage:obj.curPage,
			pageSize:obj.pageSize,
			startDay:obj.startDay,
			endDay:obj.endDay,
			cybh:obj.cybh || "",
			isRead:obj.isRead,
			keyword:obj.keyword,
			qygc:obj.qygc||"",
			fw:obj.fw
		}
	})
}

